import { FETCH_TECHNOLOGY_NEWS } from "../constants/type";

export const fetchTechnologyNews = () => (dispatch) => {
  fetch(
    "https://newsapi.org/v2/top-headlines?country=us&category=technology&apiKey=d05f10cd706c4cd18e8d483d2cb7547e"
  )
    .then((res) => res.json())
    .then((techData) =>
      dispatch({
        type: FETCH_TECHNOLOGY_NEWS,
        payload: techData,
      })
    );
};
