import {FETCH_ENTERTAINMENT, FETCH_ENTERTAINMENT_ERROR, FETCH_ENTERTAINMENT_SUCESS} from '../constants/type'

const initialState={
    entertain:[],
    loading:false

};

export default function(state=initialState,action){
    switch(action.type){
        case FETCH_ENTERTAINMENT:
            // console.log(action.payload,'reducerentertain');
            return{...state ,entertain:action.payload.articles,loading:true}
        case FETCH_ENTERTAINMENT_SUCESS:
            return{...state ,loading:false}
        case FETCH_ENTERTAINMENT_ERROR:
            return{...state ,loading:false}
        default:
            return state;
    }
}