import React from 'react'
import {connect} from 'react-redux';

 function SingleNews({url}) {
     console.log({url})
    return (
        <div>
            <iframe src= {url} frameborder="0" height='1500px' width='1500px'></iframe>

        </div>
    )
}
const mapStateToProps=(state)=>({
url:state.businessNewsReducer.singleNewsUrl,
Url:state.healthNews.singleHealth
})
export default connect( mapStateToProps,null)(SingleNews)
