import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../style/index.css";

export class Navbar extends Component {
  render() {
    return (
      <div>
        <div className="navbar navbar-expand-lg navbar-light bg-light fixed-top navbarstyle">
          <button id="menu-toggle">
          <i className="fas fa-bars"></i>
          </button>
         <div className='topnavbarstyle'>
         <Link 
              to="/"
            >
              HomePage
            </Link>
            <Link
              to="/general"
            >
              General 
            </Link>
            <Link
              to="/entertainment"
            >
              Entertainment
            </Link>
            <Link
              to="/business"
            >
              Business
            </Link>
            <Link
              to="/health"
            >
              Health
            </Link>
            <Link
              to="/science"
            >
              Science
            </Link>
            <Link
              to='/sports'
            >
              Sport
            </Link>
            <Link
              to='/technology'
            >
              Technology
            </Link>
         </div>
        </div>

        <div className="bg-light border-right" id="sidebar-wrapper">
          <div className="sidebar-heading">Start Bootstrap </div>
          <div className="list-group list-group-flush">
            <Link
              to="/"
              className="list-group-item list-group-item-action bg-light toggle  "
            >
              HomePage
            </Link>
            <Link
              to="/general"
              className="list-group-item list-group-item-action bg-light toggle selectColor "
            >
              General 
            </Link>
            <Link
              to="/entertainment"
              className="list-group-item list-group-item-action bg-light toggle"
            >
              Entertainment
            </Link>
            <Link
              to="/business"
              className="list-group-item list-group-item-action bg-light toggle"
            >
              Business
            </Link>
            <Link
              to="/health"
              className="list-group-item list-group-item-action bg-light toggle"
            >
              Health
            </Link>
            <Link
              to="/science"
              className="list-group-item list-group-item-action bg-light toggle"
            >
              Science
            </Link>
            <Link
              to='/sports'
              className="list-group-item list-group-item-action bg-light toggle"
            >
              Sport
            </Link>
            <Link
              to='/technology'
              className="list-group-item list-group-item-action bg-light toggle"
            >
              Technology
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default Navbar;
