import {FETCH_GENERAL} from '../constants/type'

export const fetchGeneral = () => (dispatch) => {
    fetch(
      "https://newsapi.org/v2/top-headlines?country=us&category=general&apiKey=d05f10cd706c4cd18e8d483d2cb7547e"
    )
      .then((res) => res.json())
      .then((generalData) =>
        dispatch({
          type: FETCH_GENERAL,
          payload: generalData,
        })
      );
  };