import {FETCH_HEALTH,FETCHSINGLE_HEALTHNEWS} from '../constants/type'

const initialState={
    healthResults:'',
    singleHealth:''
}

export default function(state=initialState,action){
    switch(action.type){
        case FETCH_HEALTH:
            console.log(action.payload,'healththreducer');
            return{...state, healthResults:action.payload.articles}
        case FETCHSINGLE_HEALTHNEWS:
            console.log(action.payload,'healthsinglethreducer');
            return{...state, singleHealth:action.payload.articles}
        default:
            return state;
    }
}