import React, { Component } from "react";
import { fetchTopHeadlines } from "../actions/homeDisplayAction";
import { connect } from "react-redux";
import NewsCard from "./common/NewsCard";
import { Link } from "react-router-dom";
import { businessNews, fetchSingleNews } from "../actions/businessActions";

class HomeDisplay extends Component {
  componentDidMount() {
    this.props.fetchTopHeadlines();
    this.props.businessNews();
  }
  handleClick = (e, url) => {
    this.props.fetchSingleNews(url);
    this.props.history.push("/single-news");
  };
  render() {
    // console.log(this.props.headlinesData, "aaaa");
    // console.log(this.props.businessValue, "heythere");
    return (
      <div className="container-fluid">
        {this.props.headlinesData.slice(3, 4).map((data) => (
          <div style={{ textAlign: "center" }} className="col-md-12">
            <h1> {data.author}</h1>
            <img style={{height:'500px'}} src={data.urlToImage} alt="" />

            {/* <h5> {data.description}</h5> */}
            <h3> {data.content}</h3>

            {/* <h5> {data.publishedAt}</h5> */}
          </div>
        ))}
        <div style={{ marginTop: "50px" }} className="row">
          <div className="col-md-2"></div>
          <div
            style={{ height: "250px", overflow: "hidden" }}
            className=" row col-md-10"
          >
            {this.props.headlinesData.slice(10, 15).map((data) => (
              <div style={{ textAlign: "center" }} className="col-md-2">
                {/* <h1 >  {data.author}</h1> */}
                <img src={data.urlToImage} alt="" height="100px" />

                {/* <h5> {data.description}</h5> */}
                <p> {data.content}</p>

                {/* <h5> {data.publishedAt}</h5> */}
              </div>
            ))}
          </div>
        </div>
        {/* business news */}
        <div className="row individualpart">
          <div className="col-md-2">
            <Link to="/business">
              {" "}
              <h5
                style={{
                  textAlign: "center",
                  marginTop: "30px",
                  color: "black",
                }}
              >
                Business
                <i
                  style={{ fontSize: "small", margin: "8px" }}
                  className="fas fa-play"
                ></i>
              </h5>
            </Link>
          </div>
          <div className="col-md-10">
            {this.props.businessValue.slice(1, 6).map((busData) => (
              <NewsCard
                handleClick={this.handleClick}
                authorName={busData.author}
                newsTitle={busData.title}
                imgUrl={busData.urlToImage}
                newsContent={busData.content}
                newsUrl={busData.url}
              />
            ))}
          </div>
        </div>
        {/* business news */}

        {/*general news  */}

        {/*general news  */}
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  headlinesData: state.topHeadlines.topHeadlines,
  businessValue: state.businessNewsReducer.business,
});

export default connect(mapStateToProps, {
  fetchTopHeadlines,
  businessNews,
  fetchSingleNews,
})(HomeDisplay);
