import React from "react";
import PropTypes from "prop-types";

export default function NewsCard({
  authorName,
  newsTitle,
  imgUrl,
  newsContent,
  newsUrl,
  handleClick,
}) {
  return (
    <>
      <div
        style={{ padding: "20px", borderBottom: "0.3px dotted gray" }}
        className="col-md-11 "
      >
        <div className="row">
          <div className="col md-10">
            <h1>{authorName} </h1>
            <h4 onClick={(e) => handleClick(e, newsUrl)}>{newsTitle}</h4>
            <p>{newsContent} </p>
          </div>
          <div className="col-md 2">
            <img
              style={{objectFit:'fill',  marginleft:'10px'}}
              src={imgUrl}
              alt="no img"
              height="200px"
              width="400px"
              
            />
          </div>
        </div>
      </div>
    </>
  );
}

NewsCard.propTypes = {
  authorName: PropTypes.string,
  newsTitle: PropTypes.string,
  imgUrl: PropTypes.string,
  
};
