import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchHealthNews,fetchSingleHealthNews } from "../actions/healthAction";
import { Link } from "react-router-dom";
import NewsCard from "./common/NewsCard";

export class Health extends Component {
  componentDidMount() {
    this.props.fetchHealthNews();
  }
  handleClick = (e, healthUrl) => {
    this.props.fetchSingleHealthNews(healthUrl);
    // <Redirect to='/single-news' />
    this.props.history.push("/single-news");
    // brower history api ..
  };

  render() {
    console.log(this.props.healthData, "11111");
    return (
      <div className="row">
        <div className="col-md-2">
          <Link to="/health">
            <h5
              style={{ textAlign: "center", marginTop: "30px", color: "black" }}
            >
              Health
              <i
                style={{ fontSize: "small", margin: "8px" }}
                className="fas fa-play"
              ></i>
            </h5>
          </Link>
        </div>
        <div className="col-md-10">
          {this.props.healthData
            ? this.props.healthData.map((busData) => (
                <NewsCard
                handleClick={this.handleClick}
                  authorName={busData.author}
                  newsTitle={busData.title}
                  imgUrl={busData.urlToImage}
                  newsContent={busData.content}
                  newsUrl={busData.url}

                />
              ))
            : ""}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  healthData: state.healthNews.healthResults,
});

export default connect(mapStateToProps, { fetchHealthNews,fetchSingleHealthNews })(Health);
