import { FETCH__SPORTS_NEWS} from '../constants/type'

const initialState={
    sports:[],
}
export default function(state=initialState,action){
    switch(action.type){
        case FETCH__SPORTS_NEWS:
            console.log(action.payload,'sportreducer');
            return{...state, sports:action.payload.articles}
        default:
            return state;

    }
}