import {FETCH_SCIENCE_NEWS} from '../constants/type'

const initialState={
    science:[],
}

export default function(state=initialState, action){
    switch(action.type){
        case FETCH_SCIENCE_NEWS:
            console.log(action.payload,'scienceReducer');
            return{...state, science:action.payload.articles}
        default:
            return state;

    }
}