import { FETCH_TOP_HEADLINES } from "../constants/type";

const initialState = {
  topHeadlines: [],
  
};
export default function (state = initialState, action) {
  switch (action.type) {
    case FETCH_TOP_HEADLINES:
    //   console.log(action.payload.articles, "reducer");
      return { ...state, topHeadlines: action.payload.articles };
    default:
      return state;
  }
}
