import { FETCH_BUSINESS,FETCH_SINGLE_NEWS } from "../constants/type";

const initialState = {
  business: [],
  singleNewsUrl:''
};

export default function (state = initialState, action) {
  switch (action.type) {
    case FETCH_BUSINESS:
      console.log(action.payload);
      return { ...state, business: action.payload.articles };
    case FETCH_SINGLE_NEWS:
      console.log(action.payload);
      return { ...state, singleNewsUrl: action.payload };

    default:
      return state;
  }
}
