import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchGeneral } from "../actions/generalActions";
import NewsCard from "./common/NewsCard";

class General extends Component {
  componentDidMount() {
    this.props.fetchGeneral();
  }

  

  render() {
    console.log(this.props.generalValue, "yyy");
    return (
      <div>
        {this.props.generalValue.map((data) => (
          <NewsCard
            handleClick={this.handleClick}
            authorName={data.author}
            newsTitle={data.title}
            imgUrl={data.urlToImage}
            newsContent={data.content}
            newsUrl={data.url}
          />
        ))}
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  generalValue: state.generalNews.general,
});

export default connect(mapStateToProps, { fetchGeneral })(General);
