import React, { Component } from "react";
import { connect } from "react-redux";
// import { Redirect } from 'react-router-dom'
import { businessNews, fetchSingleNews } from "../actions/businessActions";
import {Link} from 'react-router-dom'
import NewsCard from "./common/NewsCard";

class Business extends Component {
  componentDidMount() {
    this.props.businessNews();
  }

  handleClick = (e, url) => {
    this.props.fetchSingleNews(url);
    // <Redirect to='/single-news' />
    this.props.history.push("/single-news");
    // brower history api ..
  };

  render() {
    return (
      <div className="row">
        <div className="col-md-2">
        <Link to ='/business'>  <h5  style={{textAlign:'center',marginTop:'30px',color:"black"}} >Business<i style={{fontSize:'small' ,margin:"8px"}} className="fas fa-play"></i></h5></Link>
        </div>
        <div className="col-md-10">
          {this.props.businessValue.map((busData) => (
            <NewsCard
              handleClick={this.handleClick}
              authorName={busData.author}
              newsTitle={busData.title}
              imgUrl={busData.urlToImage}
              newsContent={busData.content}
              newsUrl={busData.url}
            />
          ))}
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  businessValue: state.businessNewsReducer.business,
});

export default connect(mapStateToProps, { businessNews, fetchSingleNews })(
  Business
);
