import { combineReducers } from "redux";
import businessReducer from "./businessReducer";
import entertainmentReducer from "./entertainmentReducer";
import generalReducer from "./generalReducer";
import healthReducer from "./healthReducer";
import homeDisplayReducer from "./homeDisplayReducer";
import scienceReducer from "./scienceReducer";
import sportsReducer from "./sportsReducer";
import technologyReducer from "./technologyReducer";

export default combineReducers({
  topHeadlines: homeDisplayReducer,
  businessNewsReducer: businessReducer,
  entertainNewsReducer:entertainmentReducer,
  healthNews:healthReducer,
  generalNews:generalReducer,
  scienceNews:scienceReducer,
  sportsNews:sportsReducer,
  technologyNews:technologyReducer,
});
