import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchTechnologyNews } from "../actions/technologyAction";
import {fetchSingleNews} from '../actions/businessActions'
import { Link } from "react-router-dom";
import NewsCard from "./common/NewsCard";

class Technology extends Component {
  componentDidMount() {
    this.props.fetchTechnologyNews();
  }

  handleClick = (e, url) => {
    this.props.fetchSingleNews(url);
    // <Redirect to='/single-news' />
    this.props.history.push("/single-news");
    // brower history api ..
  };

  render() {
    //   console.log(this.props.techData,'techcomp');
    return (
      <div>
        <div className="row">
          <div className="col-md-2">
            <Link to="/technology">
              <h5
                style={{
                  textAlign: "center",
                  marginTop: "30px",
                  color: "black",
                }}
              >
                technology
                <i
                  style={{ fontSize: "small", margin: "8px" }}
                  className="fas fa-play"
                ></i>
              </h5>
            </Link>
          </div>
          <div className="col-md-10">
            {this.props.techData.map((techData) => (
              <NewsCard
                handleClick={this.handleClick}
                authorName={techData.author}
                newsTitle={techData.title}
                imgUrl={techData.urlToImage}
                newsContent={techData.content}
                newsUrl={techData.url}
              />
            ))}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  techData: state.technologyNews.technology,
});

export default connect(mapStateToProps, { fetchTechnologyNews,fetchSingleNews })(Technology);
