import { FETCH_TOP_HEADLINES } from "../constants/type";

export const fetchTopHeadlines = () => (dispatch) => {
  fetch(
    "https://newsapi.org/v2/top-headlines?country=us&apiKey=d05f10cd706c4cd18e8d483d2cb7547e"
  )
    .then((res) => res.json())
    .then((topHeadlineData) =>
      dispatch({
        type: FETCH_TOP_HEADLINES,
        payload: topHeadlineData,
      })
    );
};
