import { FETCH_SCIENCE_NEWS } from "../constants/type";

export const fetchScienceNews = () => (dispatch) => {
  fetch(
    "https://newsapi.org/v2/top-headlines?country=us&category=science&apiKey=d05f10cd706c4cd18e8d483d2cb7547e"
  )
    .then((res) => res.json())
    .then((scienceData) =>
      dispatch({
        type: FETCH_SCIENCE_NEWS,
        payload: scienceData,
      })
    );
};
