import {
  FETCH_ENTERTAINMENT,
  FETCH_ENTERTAINMENT_SUCESS,
  FETCH_ENTERTAINMENT_ERROR,
} from "../constants/type";

export const fetchEntertainment = () => (dispatch) => {
 
  const response=
  fetch(
    "https://newsapi.org/v2/top-headlines?country=us&category=entertainment&apiKey=d05f10cd706c4cd18e8d483d2cb7547e"
  )
    .then((res) => res.json())
    .then((EntertainmentData) =>
      dispatch({
        type: FETCH_ENTERTAINMENT,
        payload: EntertainmentData,
      })
    );
    if(response){
      dispatch({
        type:FETCH_ENTERTAINMENT_SUCESS,
      })
    }
};

export const fetchEntertainmentSucess = () => (dispatch) => {
  dispatch({
    type: FETCH_ENTERTAINMENT_SUCESS,
  });
};

export const fetchEntertainmentError = () => (dispatch) => {
  dispatch({
    type: FETCH_ENTERTAINMENT_ERROR,
  });
};
