import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchScienceNews } from "../actions/scienceAction";
import {fetchSingleNews} from '../actions/businessActions'
import NewsCard from "./common/NewsCard";
import {Link} from 'react-router-dom';

class Science extends Component {
  componentDidMount() {
    this.props.fetchScienceNews();
  }

  handleClick=(e,url)=>{
    this.props.fetchSingleNews(url);
    this.props.history.push("/single-news");
  }

  render() {
    console.log(this.props.scienceValue, "123abc");
    return (
      <div className="row">
        <div className="col-md-2">
          <Link to="/science">
            {" "}
            <h5
              style={{ textAlign: "center", marginTop: "30px", color: "black" }}
            >
              science
              <i
                style={{ fontSize: "small", margin: "8px" }}
                className="fas fa-play"
              ></i>
            </h5>
          </Link>
        </div>
        <div className="col-md-10">
          {this.props.scienceValue.map((data) => (
            <NewsCard
              handleClick={this.handleClick}
              authorName={data.author}
              newsTitle={data.title}
              imgUrl={data.urlToImage}
              newsContent={data.content}
              newsUrl={data.url}
            />
          ))}
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  scienceValue: state.scienceNews.science,
});

export default connect(mapStateToProps, { fetchScienceNews,fetchSingleNews })(Science);
