import {FETCH_HEALTH,FETCHSINGLE_HEALTHNEWS} from '../constants/type'

export const fetchHealthNews=()=>(dispatch)=>{
    fetch('https://newsapi.org/v2/top-headlines?country=us&category=health&apiKey=d05f10cd706c4cd18e8d483d2cb7547e')
    .then((res)=>res.json())
    .then((healthData)=>
    dispatch({
        type:FETCH_HEALTH,
        payload:healthData
    })
    )
}
export const fetchSingleHealthNews = (healthUrl) => (dispatch) => {
    dispatch({
      type: FETCHSINGLE_HEALTHNEWS,
      payload: healthUrl,
    });
  };