import { FETCH_BUSINESS, FETCH_SINGLE_NEWS } from "../constants/type";

export const businessNews = () => (dispatch) => {
  fetch(
    "https://newsapi.org/v2/top-headlines?country=us&category=business&apiKey=d05f10cd706c4cd18e8d483d2cb7547e"
  )
    .then((res) => res.json())
    .then((businessData) =>
      dispatch({
        type: FETCH_BUSINESS,
        payload: businessData,
      })
    );
};

export const fetchSingleNews = (url) => (dispatch) => {
  dispatch({
    type: FETCH_SINGLE_NEWS,
    payload: url,
  });
};
