import { FETCH__SPORTS_NEWS } from "../constants/type";

export const fetchSportsNews = () => (dispatch) => {
  fetch(
    "https://newsapi.org/v2/top-headlines?country=us&category=sports&apiKey=d05f10cd706c4cd18e8d483d2cb7547e"
  )
    .then((res) => res.json())
    .then((sportsData) =>
      dispatch({
        type: FETCH__SPORTS_NEWS,
        payload: sportsData,
      })
    );
};
