import React, { Component } from "react";
import { fetchEntertainment,fetchEntertainmentSucess } from "../actions/entertainmentAction";
import{fetchSingleNews} from '../actions/businessActions'
import { connect } from "react-redux";
import {Link} from 'react-router-dom'
import NewsCard from "./common/NewsCard";


class Entertainment extends Component {
  componentDidMount() {
    this.props.fetchEntertainment();
  }
  handleClick = (e, url) => {
    this.props.fetchSingleNews(url);
    // <Redirect to='/single-news' />
    this.props.history.push("/single-news");
    // brower history api ..
  };

  render() {
    // console.log(this.props.entertainData, "ent");
    // this.props.entertainData.length !===0 ? this.props.fetchEntertainmentSucess:''
    return (
      <div className="row">
        {/* { <div  style={{position:'absolute',top:'50%' ,left:'50%'}} class="spinner-border" role="status">
          <span class="sr-only">Loading...</span>
        </div> } */}
        <div className="col-md-2">
        <Link to ='/entertainment'>  <h5  style={{textAlign:'center',marginTop:'30px',color:"black"}} >Entertainment<i style={{fontSize:'small' ,margin:"8px"}} className="fas fa-play"></i></h5></Link>

        </div>
        <div className="col-md-10">
          {this.props.entertainData.map((busData) => (
           <NewsCard
           handleClick={this.handleClick}
           authorName={busData.author}
           newsTitle={busData.title}
           imgUrl={busData.urlToImage}
           newsContent={busData.content}
           newsUrl={busData.url}
         />
          ))}
        </div>
        
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  entertainData: state.entertainNewsReducer.entertain,
});

export default connect(mapStateToProps, { fetchEntertainment,fetchEntertainmentSucess,fetchSingleNews })(Entertainment);
