import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchSportsNews } from "../actions/sportsAction";
import {fetchSingleNews} from '../actions/businessActions'
import {Link} from 'react-router-dom'
import NewsCard from './common/NewsCard'
class Sports extends Component {
  componentDidMount() {
    this.props.fetchSportsNews();
  }

  handleClick=(e, url)=>{
    this.props.fetchSingleNews(url)
    this.props.history.push("/single-news");
  }

  render() {
    console.log(this.props.sportsValue, "sportcomponent");
    return (
      <div className="row">
        <div className="col-md-2">
          <Link to="/sports">
            {" "}
            <h5
              style={{ textAlign: "center", marginTop: "30px", color: "black" }}
            >
              sports
              <i
                style={{ fontSize: "small", margin: "8px" }}
                className="fas fa-play"
              ></i>
            </h5>
          </Link>
        </div>
        <div className="col-md-10">
          {this.props.sportsValue.map((sportData) => (
            <NewsCard
              handleClick={this.handleClick}
              authorName={sportData.author}
              newsTitle={sportData.title}
              imgUrl={sportData.urlToImage}
              newsContent={sportData.content}
              newsUrl={sportData.url}
            />
          ))}
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  sportsValue: state.sportsNews.sports,
});

export default connect(mapStateToProps, { fetchSportsNews,fetchSingleNews })(Sports);
